/*----------bg----------*/
.login_bg{
    background-size: 120%;
    background-position: center 70%;
    position: absolute;
    bottom: -200px;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
  }
  
  @media (min-width: 992px){
    .login_bg{
      background-size: 100%;
      background-position: center 60%;
      bottom: 0;
    }
    .login_bg-h{
      height: 80vh;
    }
    
  }
  
  @media (min-width: 768px){
    .login_bg-h{
      height: 73vh;
    }
    
  }
  
  h1 a{
    text-indent: 101%;
    overflow: hidden;
      width: 96px;
      height: 24px;
      white-space: nowrap;
      display: inline-block;
  }