h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6{
    margin-bottom: 0;
    font-family: "Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", "Helvetica Neue", Arial, sans-serif;
    font-weight: 700;
    line-height: 1.2;
  }

  body{
    font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", "Helvetica Neue", Arial, sans-serif;
    color: #0000009A;
    font-weight: 400;
    line-height: 1.5;
  }

ol, ul {
	list-style: none;
  padding: 0;
}

a{
  display: block;
  text-decoration: none!important;
  color: unset;
  &:hover{
    color: #6C51E2;
  }
}

img{
  max-width:100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.img-hover{
    transition: transform 0.5s ease-out;
    &:hover{
        transform: scale(1.1, 1.1);
    }
}

.img-center{ 
    object-position: center center;
    object-fit: cover;
}

.overflow-hid{
  overflow: hidden;
}

.material-icons{
  vertical-align: text-bottom;
}

.bg-cover{
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.text-roboto{
  font-family: "Roboto", sans-serif;
}

.text-gray610{
  color: #00000099 !important;
}

.btn-white{
  background-color: #fff;
  border-color: #fff;
}

.btn-primary{
  color: #fff;
  background-color: #6C51E2;
  border-color: #6C51E2;
  &:hover{
    color: #fff;
    background-color: #3E21C7;
    border-color: #3E21C7;
  }
}

.btn-outline-primary{
  color: #6C51E2;
  border-color: #6C51E2;
  &:hover{
    color: #fff;
    background-color: #3E21C7;
    border-color: #3E21C7;
  }
}

.text-left{
    text-align: left !important;
  }
  
  .w-100{
    width: 100% !important;
  }
  
  .navbar-light{
    background: #F1F0F8!important;
  }
  
  .bg-light{
    background: #F1F0F8!important;
  }

  .border-primary{
    border-color: #6C51E2 !important;
  }

  .text-title{
    font-family: 'Raleway', sans-serif;
  }

  a.insidetext{
    display: inline;
    color: #6C51E2;
  }

  .bg-gray910{
    background-color: #121212 !important;
}

.text-info{
    color: #0000009A !important;
  }

  .page-item.active .page-link{
    z-index: 3;
    color: #fff;
    background-color: #6C51E2;
    border-color: #6C51E2;
  }
  
  .page-link{
    color: #6C51E2;
  }

  .text-black{
    color: #000 !important;
  }

  .btn-black{
    color: #fff;
    background-color: #000;
    border-color: #000;
    &:hover{
      color: #000;
      background-color: #fff;
      border-color: #000;
    }
  }

  .text-info{
    color: #0000009A !important;
  }

  .btn-outline-white{
    color: #fff;
    border-color: #fff;
    &:hover{
      color: #000;
      background: #fff;
    }
  }

  .badge-primary{
    color: #fff;
    background-color: #6C51E2;
  }

