/*----------banner----------*/
@media (min-width: 768px){
    .swiper-container.banner .pt_adjust_swiper{
      padding-top: 11.5%;
      padding-bottom: 11.5%;
    }
    .text-xxl-md{
      font-size: 48px;
    }
  }
  
  .swiper-container.banner .swiper-slide{
    height: unset !important;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .img-mask::before{
    content: "";
  }
  
  .img-mask::before, .img-mask-rounded::before, .hover-mask:hover::after{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .z-index-1{
    position: relative;
    z-index: 1;
  }
  
  .font-weight-extrabold{
    font-weight: 800;
  }
  
  .banner{
    height: 420px;
  }
  
  /*----------search-bar----------*/
  .search-bar .row{
    transform: translateY(-50px);
    position: relative;
    z-index: 3;
  }
  
  .search-bar .dropdown{
    width: 100%;
  }
  
  @media (min-width: 992px){
    .search-bar .dropdown{
      width: 26.7%;
    }
  }
  
  
  
  /*----------Top Choices----------*/
  
  .nav-tabs{
    margin-bottom: 24px;  
  }
  .nav-link.active,.nav-link:hover {
    border-width:0;
    border-bottom:2px solid #6C51E2;
  }
  .card{
    border:0;
  }
  .card-body{
    padding:0;
  }
  .card-img-top{
    height:175px;
    object-fit:cover;
  }
  .swiper-button-prev{
    display:none;
  }
  
  .swiper-container2{
    overflow: hidden;
  }
  
  /*----------Popular Destinations----------*/
  .position-r{
    position: relative;
  }
  
  .img-mask-rounded::before{
    content: "";
    background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.37) 38%, rgba(0, 0, 0, 0.65) 66%);
    border-radius: 8px;
    
  }
  
  /*----------Popular Destinations----------*/
  .swiper-container3{
    overflow: hidden;
  }