/*----------navbar----------*/

.navbar-brand{
    background: url("https://ppt.cc/fFNjjx@.png") no-repeat center center;
    background-size: 100%;
    text-indent: 101%;
    overflow: hidden;
    width: 96px;
    height: 24px;
    white-space: nowrap;
    display: inline-block;
    margin: 0 auto;
  }

.collapse-position{
  position: absolute;
  top: 60px;
  left: 0;
  background-color: #fff;
  width: 100%;
  z-index: 2;
}

@media (min-width: 768px){
  .collapse-position{
    position: static;
  }
}

.navbar-light .navbar-nav .nav-link{
  color: #000;
}